import { GoogleComponent } from 'react-google-location';

import React, { Component } from 'react';

const API_KEY = 'AIzaSyAn7yYkPyVP-yfmyg2AlSlumFqstnUtUpM';

class HomeComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      place: null,
    };
  }

  render() {
    return (
      <div>
        <GoogleComponent
          apiKey={API_KEY}
          language={'en'}
          country={'country:in|country:us'}
          coordinates={true}
          onChange={(e) => {
            this.setState({ place: e });
            this.props.SetLocation(this.state.place.coordinates);
          }}
        />
      </div>
    );
  }
}

export default HomeComponent;
